<template>
  <div class="resetPassWord">
    <mu-form ref="form" :model="validateForm" class="card-content">
      <mu-form-item label="" prop="oldPassWord" :rules="oldPassWordRules">
        <mu-text-field type="password" v-model="validateForm.oldPassWord" prop="oldPassWord"></mu-text-field>
      </mu-form-item>
      <mu-form-item label="" prop="newPassWord" :rules="newPassWordRules">
        <mu-text-field type="password" v-model="validateForm.newPassWord" prop="newPassWord"></mu-text-field>
      </mu-form-item>
      <mu-form-item label="" prop="newPassWord2" :rules="newPassWord2Rules">
        <mu-text-field type="password" v-model="validateForm.newPassWord2" prop="newPassWord2"></mu-text-field>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" @click="submit"></mu-button>
      </mu-form-item>
    </mu-form>
    <div class="tip">
      <p class="title"></p>
      <p class="value">1.</p>
      <p class="value">2.3</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "CaResetPassWordrd",
  data() {
    return {
      oldPassWordRules: [{ validate: val => !!val, message: "" }],
      newPassWordRules: [
        { validate: val => !!val, message: "" },
        { validate: val => val.length >= 6 && val.length <= 20, message: "620"}
      ],
      newPassWord2Rules: [
        { validate: val => !!val, message: "" },
        { validate: val => (val == this.validateForm.newPassWord), message: "2"}
      ],
      validateForm: {
        oldPassWord: "",
        newPassWord: "",
        newPassWord2: ""
      }
    };
  },
  methods: {
    submit() {
      this.$refs.form.validate().then(result => {
        if (result) {
          const param = {
            currentPassword: this.validateForm.oldPassWord,
            newPassword: this.validateForm.newPassWord,
          };
          this.$http.post('/api/services/app/Profile/ChangePassword', param)
          .then((response) => {
            if (response.data.success) {
              this.$toast.message("");
              setTimeout(() => {
                this.$router.push('/MyCenter');
              }, 2000);
            }
          })
          .catch(error => {
            this.$toast.message(error.response.data.error.message);
          });
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



