<template>
  <!--  -->
  <div class="recharge">
    <mu-list>
      <mu-list-item avatar button>
        <mu-list-item-action>
          <img src="../../assets/images/logo.png" alt="">
        </mu-list-item-action>
        <mu-list-item-title v-if="coinInfo.virtualCoin">{{coinInfo.virtualCoin.name}}/{{coinInfo.virtualCoin.displayName}}</mu-list-item-title>
      </mu-list-item>
    </mu-list>
    <div class="imtoken" v-if="coinInfo.virtualCoin">
      <div class="QRcode">
        <div id="qr"></div>
      </div>
      <!-- <div></div> -->
      <div class="text">{{address}}</div>
      <div class="btn-copy" primary v-clipboard:copy="address" v-clipboard:success="onCopy" v-clipboard:error="onError"></div>
      <div class="tip"></div>
    </div>
    <div class="prompt" v-if="coinInfo.virtualCoin">
      <p class="title"></p>
      <p><span>{{coinInfo.virtualCoin.name}}</span><br>{{coinInfo.virtualCoin.name}}2<br><br>Bitstamp) </p>
    </div>
  </div>   
</template>

<script>
import QRCode from "../bin/qr";
export default {
  name: "Recharge",
  data() {
    return {
      coinInfo: {},
      address: "",
      sharCode: ""
    };
  },
  props:['id'],
  mounted() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      this.$http('/api/services/app/Wallet/GetMyWalletBalances')
      .then((response) => {
        const virtualCoinList = response.data.result.items;
        this.coinInfo = virtualCoinList.find((item, index, arr) => {
          return item.id == this.id
        })
        const param = {
          WalletId: this.id,
        };
        this.$http('/api/services/app/Wallet/GetMyDepositAddress', { params: param })
        .then((response) => {
          this.address = response.data.result.address;
          let width = window.innerWidth / 1.9;
          let qr = new QRCode(document.getElementById("qr"), {
            width: width,
            height: width,
            colorDark: "#000000",
            colorLight: "rgba(255,255,255,1)"
          });
          qr.makeCode(this.address);
        })
      })
      .catch((error) => {
        this.$toast.message(error.response.data.error.message);
      });
    },
    //
    onCopy: function(e) {
      this.$toast.message("");
    },
    onError: function(e) {
      this.$toast.message("");
    }
  }
};
</script>
<style scoped>
.QRcode{ margin-top: 20px; background: #fff; padding: 0 8px; display: flex; justify-content: center;}
</style> 



