// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import MuseUI from 'muse-ui';
import 'muse-ui/dist/muse-ui.css';
import './assets/css/main.css'; //
import Toast from 'muse-ui-toast'; // 
import VueClipboard from 'vue-clipboard2' //

axios.defaults.baseURL='https://api.hibi.com';
axios.defaults.headers.common['Content-Type'] = 'application/json-patch+json';
const token = sessionStorage.getItem('accessToken') || 'null';
axios.defaults.headers.common.authorization = `Bearer ${token}`;
axios.defaults.headers.common['abp-tenantid'] = '1';

Vue.prototype.$http = axios;

Vue.use(MuseUI)
Vue.use(Toast,{
  position: 'bottom',               // 
  time: 3000,                       // 
  closeIcon: 'close',               // 
  close: false,                      // 
  successIcon: 'check_circle',      // 
  infoIcon: 'info',                 // 
  warningIcon: 'priority_high',     // 
  errorIcon: 'warning'              // 
})
Vue.use(VueClipboard)

Vue.config.productionTip = false

//
router.beforeEach((to,from,next)=>{
  const token = sessionStorage.getItem("accessToken")
  // 
  if(to.path === '/Login' || token || to.path === '/Register' || to.path === '/Forget'){
    next()
  }else{
    next("/Login")
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
})



