<template>
  <!-- google -->
  <div class="google">
    <div class="vsteper-container">
      <mu-stepper :active-step="vactiveStep">
        <mu-step>
          <mu-step-label>
            
          </mu-step-label>
          <mu-step-content>
            <p>
              , Wifi
            </p>
            <mu-button class="step-button" @click="vhandleNext" color="primary"></mu-button>
          </mu-step-content>
        </mu-step>
        <mu-step>
          <mu-step-label>
            
          </mu-step-label>
          <mu-step-content>
            <p>
              5018-25
            </p>
            <mu-button class="step-button" @click="vhandleNext" color="primary"></mu-button>
            <mu-button flat class="step-button" @click="vhandlePrev"></mu-button>
          </mu-step-content>
        </mu-step>
        <mu-step>
          <mu-step-label>
            
          </mu-step-label>
          <mu-step-content>
            <p>
              ok
            </p>
            <mu-button class="demo-step-button" @click="vhandleNext" color="primary"></mu-button>
            <mu-button flat class="demo-step-button" @click="vhandlePrev"></mu-button>
          </mu-step-content>
        </mu-step>
      </mu-stepper>
      <p v-if="vfinished">
        !<a href="javascript:;" @click="vreset"></a>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GoogleVerify',
  data () {
    return {
      vactiveStep: 0
    }
  },
  computed: {
    vfinished () {
      return this.vactiveStep > 2;
    }
  },
  methods: {
    vhandleNext () {
      this.vactiveStep++;
    },
    vhandlePrev () {
      this.vactiveStep--;
    },
    vreset () {
      this.vactiveStep = 0;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mu-step-label-icon-container{ display: none;}
.vsteper-container{
}
.step-content {
}
.step-button {
  
}
</style>



