<template>
  <!--  -->
  <div class="recharge cash" v-if="coinInfo.virtualCoin">
    <mu-list>
      <mu-list-item avatar button>
        <mu-list-item-action>
          <img src="../../assets/images/logo.png" alt="">
        </mu-list-item-action>
        <mu-list-item-title>{{coinInfo.virtualCoin.name}}/{{coinInfo.virtualCoin.displayName}}</mu-list-item-title>
      </mu-list-item>
    </mu-list>
    <div class="balance"><span><font></font>{{coinInfo.banlance.toFixed(coinInfo.virtualCoin.decimals)}}</span><span>{{coinInfo.virtualCoin.name}}</span></div>
    <div class="addressInput">
      <mu-text-field v-model="address" placeholder=""></mu-text-field><br/>
      <div class="right">
        <!-- <span class="icon iconfont" @click="scan">&#xe621;</span> -->
        <router-link to="/Address"><span class="icon iconfont">&#xe632;</span></router-link>
      </div>
    </div>
    <div class="inputTip">{{coinInfo.virtualCoin.minWithdrawAmount}},{{coinInfo.virtualCoin.maxWithdrawAmount}}<br><span></span></div>
    <div class="addressInput">
      <mu-text-field v-model="count" type="number" placeholder=""></mu-text-field><br/>
      <div class="right right2" @click="getAllCash"><span></span></div>
    </div>
    <div class="inputTip2"><span> {{finalFee}}</span><span> {{realCount}}</span></div>
    <div class="prompt">
      <p class="title"></p>
      <p>USDTUSDT</p>
    </div>
    <div class="botButton">
      <mu-button class="gray" @click="routerBack"></mu-button>
      <mu-button color="primary" @click="submit"></mu-button>
    </div>
  </div>   
</template>

<script>
import QRCode from "../bin/qr";
export default {
  name: "Cash",
  data() {
    return {
      coinInfo: {},
      address: "", // 
      count: 0,
      finalFee: 0, // 
      realCount: 0 // 
    };
  },
  props:['id'],
  created() {
    this.getInfo();
  },
  watch: {
    count() {
      if(!this.count || this.count == 0) {
        this.finalFee = 0;
        this.realCount = 0;
      }else if(this.count > this.coinInfo.virtualCoin.maxWithdrawAmount || this.count > this.coinInfo.banlance) {
        // 
        this.count = Math.min(this.coinInfo.banlance, this.coinInfo.virtualCoin.maxWithdrawAmount)
      }else{
        let fee = Math.max(this.count * this.coinInfo.virtualCoin.withdrawFeeRate, this.coinInfo.virtualCoin.withdrawFeeMax);
        this.finalFee = Math.min(fee, this.coinInfo.virtualCoin.withdrawFeeMin).toFixed(this.coinInfo.virtualCoin.decimals);
        this.realCount = (this.count - this.finalFee).toFixed(this.coinInfo.virtualCoin.decimals);
      }
    }
  },
  methods: {
    getInfo() {
      this.$http('/api/services/app/Wallet/GetMyWalletBalances')
      .then((response) => {
        const virtualCoinList = response.data.result.items;
        this.coinInfo = virtualCoinList.find((item, index, arr) => {
          return item.id == this.id
        })
        console.log(this.coinInfo)
      })
      .catch((error) => {
        this.$toast.message(error.response.data.error.message);
      });
    },
    // 
    getAllCash() {
      this.count = this.coinInfo.virtualCoin.maxWithdrawAmount.toFixed(this.coinInfo.virtualCoin.decimals);
    },
    // 
    scan() {

    },
    // 
    submit() {
      console.log(this.count,this.address)
      if(!this.count || !this.address){
        this.$toast.message("");
      }else{
        const param = {
          address: this.address,
          amount: this.count,
          walletId: this.id,
        };
        this.$http.post('/api/services/app/Wallet/CreateWithdraw', param)
        .then((response) => {
          if (response.data.success) {
            this.$toast.message("!");
          }
        })
        .catch((error) => {
          this.$toast.message("");
        });
      }
    },
    routerBack() {
      this.$router.back();
    }
  }
};
</script>
<style scoped>
.QRcode{ margin-top: 20px; background: #fff; padding: 0 8px;}
.cash .prompt{ padding: 20px 15px; margin-top: 50px;}
</style> 



