<template>
  <!--  -->
  <div class="card">
    <div class="tip"></div>
    <mu-form ref="form" :model="validateForm" class="card-content">
      <mu-form-item label="" prop="name" :rules="nameRules">
        <mu-text-field v-model="validateForm.name" prop="name"></mu-text-field>
      </mu-form-item>
      <mu-form-item label="" prop="cardId" :rules="cardIdRules">
        <mu-text-field v-model="validateForm.cardId" prop="cardId"></mu-text-field>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" @click="submit"></mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>

<script>
export default {
  name: 'Card',
  data () {
    return {
      nameRules: [
        { validate: (val) => !!val, message: ''},
      ],
      cardIdRules: [
        { validate: (val) => !!val, message: ''},
        { validate: (val) => (/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(val)), message: ''}
      ],
      validateForm: {
        name: '',
        cardId: '',
      }
    }
  },
  methods: {
    submit() {
      this.$refs.form.validate().then((result) => {
        if(result){

        }
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



