import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/components/login/Login'
import Register from '@/components/login/Register'
import Forget from '@/components/login/Forget'

import Index from '@/components/Index'
import ChooseCoin from '@/components/ChooseCoin'
import Search from '@/components/Search'
import Details from '@/components/Details'
import Deal from '@/components/Deal'
import Entrust from '@/components/Entrust'
import EntrustDetails from '@/components/EntrustDetails'
// 
import MyCenter from '@/components/mycenter/Index'
import Card from '@/components/mycenter/Card'
import Qusetion from '@/components/mycenter/Qusetion'
import QusetionList from '@/components/mycenter/QusetionList'
import AboutUs from '@/components/mycenter/AboutUs'
import About from '@/components/mycenter/About'
import Contact from '@/components/mycenter/Contact'
import Notice from '@/components/mycenter/Notice'
import Safe from '@/components/mycenter/Safe'
import ResetPassWord from '@/components/mycenter/ResetPassWord'
import EmailVerify from '@/components/mycenter/EmailVerify'
import PhoneVerify from '@/components/mycenter/PhoneVerify'
import GoogleVerify from '@/components/mycenter/GoogleVerify'
// 
import Property from '@/components/property/Index'
import Recharge from '@/components/property/Recharge'
import Cash from '@/components/property/Cash'
import Address from '@/components/property/Address'
import Historys from '@/components/property/Historys'



Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', name: 'home', redirect:"/Index" },
    { path: '/Index', name: '', component: Index, meta: { keepAlive: true} },
    { path: '/Login', name: '', component: Login, meta: { keepAlive: true},
      beforeEnter:(to,from,next)=>{
        // ,
        const token = sessionStorage.getItem("accessToken");
        if(token){
          next("/Index");
        }else{
          next()
        }
      }
    },
    { path: '/Register', name: '', component: Register, meta: { keepAlive: true}},
    { path: '/Forget', name: '', component: Forget, meta: { keepAlive: true} },
    { path: '/Deal', name: '', component: Deal, },
    { path: '/Deal/:id', name: '', component: Deal, props: true,},
    { path: '/ChooseCoin', name: '', component: ChooseCoin, meta: { keepAlive: true} },
    { path: '/Search', name: '', component: Search, meta: { keepAlive: true} },
    { path: '/Details/:id', name: '', component: Details, props: true },
    { path: '/Entrust', name: '', component: Entrust },
    { path: '/EntrustDetails/:id', name: '', component: EntrustDetails, props: true },
    { path: '/MyCenter', name: '', component: MyCenter },
    { path: '/Card', name: '', component: Card, meta: { keepAlive: true} },
    { path: '/Qusetion', name: '', component: Qusetion, meta: { keepAlive: true} },
    { path: '/QusetionList', name: '', component: QusetionList },
    { path: '/AboutUs', name: '', component: AboutUs, meta: { keepAlive: true} },
    { path: '/About', name: '', component: About, meta: { keepAlive: true} },
    { path: '/Contact', name: '', component: Contact, meta: { keepAlive: true} },
    { path: '/Notice', name: '', component: Notice, meta: { keepAlive: true} },
    { path: '/Safe', name: '', component: Safe, meta: { keepAlive: true} },
    { path: '/ResetPassWord', name: '', component: ResetPassWord, meta: { keepAlive: true} },
    { path: '/EmailVerify', name: '', component: EmailVerify, meta: { keepAlive: true} },
    { path: '/PhoneVerify', name: '', component: PhoneVerify, meta: { keepAlive: true} },
    { path: '/GoogleVerify', name: '', component: GoogleVerify, meta: { keepAlive: true} },
    { path: '/Property', name: '', component: Property, },
    { path: '/Recharge/:id', name: '', component: Recharge, props: true, },
    { path: '/Cash/:id', name: '', component: Cash, props: true,},
    { path: '/Historys/:id', name: '', component: Historys, props: true,},
    { path: '/Address', name: '', component: Address },
  ],
  linkActiveClass: 'router-link-active'
})



